import * as React from "react";
import {
  AppBar,
  Box,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from "@mui/material";
import {
  AccountBalanceWalletOutlined,
  AccountCircle,
  Adb as AdbIcon,
  BubbleChart,
  Logout,
  Person,
  PhoneAndroid,
  Toll,
} from "@mui/icons-material";
import axios from "axios";

const ResponsiveAppBar = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [user, setUser] = React.useState(null);

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenu = (event) => {
    if (localStorage.getItem("user_uuid")) setAnchorEl(event.currentTarget);
  };
  const getUserData = async () => {
    const result = await axios({
      method: "get",
      url: "/users/getDetails/" + localStorage.getItem("user_uuid"),

      headers: {
        "Content-Type": "application/json",
      },
    });
    console.log(result);
    if (result.data.success) setUser(result.data.userData);
  };

  React.useEffect(() => {
    if (anchorEl && localStorage.getItem("user_uuid")) getUserData();
  }, [anchorEl]);
  return (
    <AppBar position="static" sx={{ backgroundColor: "#e72a41" }}>
      <Container maxWidth="xl">
        <Toolbar disableGutters sx={{ justifyContent: "space-between" }}>
          <BubbleChart sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: "none", md: "flex" },
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            LOGO
          </Typography>
          <AdbIcon sx={{ display: { xs: "flex", md: "none" }, mr: 1 }} />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: "flex", md: "none" },
              flexGrow: 1,
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            Skill Based Games
          </Typography>

          <Box sx={{ flexGrow: 0, justifyContent: "flex-end" }}>
            <Tooltip title="Profile">
              <IconButton sx={{ p: 0 }} onClick={handleMenu}>
                {/* <Avatar
                  alt="Remy Sharp"
                  src="https://mui.com/static/images/avatar/2.jpg"
                /> */}
                <AccountCircle
                  style={{
                    backgroundColor: "#fff",
                    color: "#00b8ff",
                    borderRadius: "20px",
                  }}
                />
              </IconButton>
            </Tooltip>

            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              sx={{ mt: "40px" }}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>
                <AccountBalanceWalletOutlined /> &nbsp;&nbsp; <b>Ballance :</b>{" "}
                {user?.coin_balance || 0}
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <PhoneAndroid /> &nbsp;&nbsp; <b>Mobile :</b>{" "}
                {user?.user_mobile || 0}
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <Person /> &nbsp;&nbsp; <b>UserName :</b>{" "}
                {user?.ludo_king_username || "-"}
              </MenuItem>
              <MenuItem
                onClick={() => {
                  window.location.assign("/buychips");
                }}
              >
                <Toll /> &nbsp;&nbsp; <b>Buy Chips</b>
              </MenuItem>
              <MenuItem
                onClick={() => {
                  localStorage.clear();
                  window.location.assign("/main");
                }}
              >
                <Logout /> &nbsp;&nbsp; <b>Logout</b>
              </MenuItem>
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default ResponsiveAppBar;
