import axios from "axios";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import MainPage from "./Pages/MainPage";
import io from "socket.io-client";
import Admin from "./Pages/Admin";
import Dashboard from "./Pages/Dashboard";
import Test from "./Components/test";
import Test2 from "./Components/test2";
import BuyChips from "./Pages/BuyChips";
// export const ServerAddress = "http://localhost:9000";
export const ServerAddress = "http://api.ludojunction.com/";
export const socket = io.connect(ServerAddress, {
  query:
    "user_uuid=" + (localStorage.getItem("admin_uuid")
      ? "Admin"
      : localStorage.getItem("user_uuid")),
});
axios.defaults.baseURL = ServerAddress;
function App() {
  return (
    <div className="App">
      <Router>
        <Routes>
          <Route path="*" element={<Navigate replace to={"/main"} />} />
          <Route path="/main" element={<Dashboard />} />
          <Route path="/LudoMain" element={<MainPage />} />
          <Route path="/SnakeMain" element={<MainPage />} />
          <Route path="/admin" element={<Admin />} />
          <Route path="/test" element={<Test />} />
          <Route path="/test2" element={<Test2 />} />
          <Route path="/buychips" element={<BuyChips />} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
