import axios from "axios";
import React, { useEffect, useState } from "react";

const PendingChallengesList = ({ setSelectiveChallenge }) => {
  const [challenges, setChallenges] = useState([]);
  const getChallenges = async () => {
    const result = await axios({
      method: "get",
      url: "/challenges/getReportChallenges",

      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setChallenges(result.data.result);
  };
  useEffect(() => {
getChallenges();


    return () => {};
  }, []);
  return challenges.length ? (
    challenges.map((item, i) => (
      <div
        className="main_challeges_box"
        onClick={() => setSelectiveChallenge(item)}
        key={item.challenge_uuid}
      >
        <div className="main_challeges_box_user">
          <div className="main_challeges_box_user_profile">Profile</div>
          {item.created_username}
        </div>
        <div className="main_challeges_box_details">
          <h1>{item.coins} Coins</h1>
        </div>
      </div>
    ))
  ) : (
    <div className="main_challeges_box">
      <div className="main_challeges_box_details">
        <h1>No Challenges</h1>
      </div>
    </div>
  );
};

export default PendingChallengesList;
