import React, { useEffect, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";
import axios from "axios";
import { ServerAddress, socket } from "../App";
import "./style.css";
import userImage from "../assets/user.png";

const ChallengeReport = ({ close, challenge }) => {
  const [user, setUsers] = useState("");
  const [challengeData, setChallengData] = useState({});

  useEffect(() => {
    setChallengData(challenge);
  }, [challenge]);

  const VarifyUser = async () => {
    if (!user) return;
    const result = await axios({
      method: "put",
      url: "/challenges/adminDecision",
      data: {
        challenge_uuid: challengeData.challenge_uuid,
        winer: user,
        loser:
          user === challengeData?.created_by
            ? challengeData?.accepted_by
            : challengeData?.created_by,
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) close();
    socket.emit("update", {});
  };

  return (
    <div className="overlay">
      <div className="popup_background">
        <CloseIcon className="popup_nav_icon" onClick={close} />
        <div className="popup_nav">
          <h2>Challenge Decision</h2>
          <div className="report_columns">
            <div
              className={`report_container ${
                user === challengeData?.created_by
                  ? "report_container_active"
                  : ""
              }`}
              onClick={() => setUsers(challengeData?.created_by)}
              style={{ backgroundColor: "#8ac06d" }}
            >
              <h5>
                {challengeData?.created_username}
                {user === challengeData?.created_by ? (
                  <span> (Winner)</span>
                ) : (
                  ""
                )}
              </h5>

              <img
                src={
                  ServerAddress +
                  challengeData?.images?.find(
                    (a) => a.user_uuid === challengeData?.created_by
                  )?.image_url
                }
                alt={challengeData?.created_by}
                onError={(e) => (e.target.src = userImage)}
              />
              <h5>Positive Score :{challengeData?.created_positive_score}</h5>
              <h5>Nagative Score :{challengeData?.created_nagative_score}</h5>
            </div>
            <div
              className={`report_container ${
                user === challengeData?.accepted_by
                  ? "report_container_active"
                  : ""
              }`}
              onClick={() => setUsers(challengeData?.accepted_by)}
              style={{ backgroundColor: "#fdbb58" }}
            >
              <h5>
                {challengeData?.accepted_username}{" "}
                {user === challengeData?.accepted_by ? (
                  <span>(Winner)</span>
                ) : (
                  ""
                )}
              </h5>
              <img
                src={
                  ServerAddress +
                  challengeData?.images?.find(
                    (a) => a.user_uuid === challengeData?.accepted_by
                  )?.image_url
                }
                alt={challengeData?.accepted_by}
                onError={(e) => (e.target.src = userImage)}
              />
              <h5>Positive Score :{challengeData?.accepted_positive_score}</h5>
              <h5>Nagative Score :{challengeData?.accepted_nagative_score}</h5>
            </div>
          </div>
        </div>
        <button className="popup_submit_btn" type="button" onClick={VarifyUser}>
          Done
        </button>
      </div>
    </div>
  );
};

export default ChallengeReport;
