import React, { useReducer, useState } from "react";
import PageLayout from "./PageLayout";
import {
  FormControl,
  Select,
  MenuItem,
  InputLabel,
  TextField,
  Button,
} from "@mui/material";
import axios from "axios";
const transectionReducer = (state, action) => {
  switch (action.type) {
    case "transection_type":
      return { ...state, transection_type: action.payload };
    case "transection_id":
      return { ...state, transection_id: action.payload };
    case "amount":
      return { ...state, amount: action.payload };
    case "clear":
      return {
        transection_type: "",
        transection_id: "",
        amount: "",
        code: 1234,
      };
    default:
      return state;
  }
};
const BuyChips = () => {
  const [transection, dispatchTransection] = useReducer(transectionReducer, {
    transection_type: "",
    transection_id: "",
    amount: "",
    code: 1234,
  });
  const [notifications, setNotification] = useState(false);
  const PostTransectionData = async () => {
    const result = await axios({
      method: "post",
      url: "/transection/posttransaction/",
      data: { ...transection, user_uuid: localStorage.getItem("user_uuid") },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) dispatchTransection({ type: "clear" });
  };
  const content = (
    <div className="buyCoin">
      <div className="buyCoin_nav">Buy Chips (1Rupee = 1Chip)</div>
      <div>
        <h3>Pay via Paytm or UPI OnLy</h3>
        <h4>
          Please pay at this number only <br /> 012345678 and Enter <br />
          Transection ID Below
        </h4>
        <div className="buyCoin_body">
          <FormControl fullWidth className="Inputs">
            <InputLabel>Transection Type</InputLabel>
            <Select
              id="outlined-size-small"
              size="small"
              style={{ color: "#000" }}
              value={transection.transection_type}
              label="Age"
              onChange={(e) =>
                dispatchTransection({
                  type: "transection_type",
                  payload: e.target.value,
                })
              }
            >
              <MenuItem value="Paytm Wallet">Paytm Wallet</MenuItem>
              <MenuItem value="Phonepy UPI">Phonepy UPI</MenuItem>
            </Select>
          </FormControl>
          <FormControl fullWidth className="Inputs code">
            <TextField
              autoFocus={true}
              id="outlined-size-small"
              size="small"
              type="text"
              placeholder="Code"
              value={transection.code}
              disabled
              autocomplete="off"
              fullWidth
            />
            <Button
              variant="contained"
              onClick={() => {
                navigator.clipboard.writeText(transection.code);
                setNotification("Transection Code Copied");
                setTimeout(() => setNotification(false), 3000);
                return;
              }}
            >
              Copy
            </Button>
          </FormControl>
          <FormControl fullWidth className="Inputs">
            <TextField
              autoFocus={true}
              id="outlined-size-small"
              size="small"
              type="text"
              placeholder="Tansection ID"
              value={transection.transection_id}
              onChange={(e) =>
                dispatchTransection({
                  type: "transection_id",
                  payload: e.target.value,
                })
              }
              autocomplete="off"
            />
          </FormControl>
          <FormControl fullWidth className="Inputs">
            <TextField
              autoFocus={true}
              id="outlined-size-small"
              size="small"
              type="number"
              placeholder="Amount"
              value={transection.amount}
              onChange={(e) =>
                dispatchTransection({
                  type: "amount",
                  payload: e.target.value,
                })
              }
              autocomplete="off"
            />
          </FormControl>
        </div>
        <Button variant="contained" onClick={PostTransectionData}>
          Load
        </Button>
      </div>
      {/* 
      {navMenu.map((item) => (
        <div
          className={`main_nav_content${
            activeNav === item ? " main_nav_content_active" : ""
          }`}
          onClick={() => {
            setActiveNav(item);
          }}
          key={item}
        >
          {" "}
          <GameIcon sx={{ marginRight: "8px" }} />
          {item}
      
      ))}
    </div>
    <div className="dashboard_body">
      {games.map((item, i) => (
        <div
          className="dashboard_challeges_box"
          onClick={() => window.location.assign(`/${item}Main`)}
          style={{
            // background: i % 2 === 0 ? "#8ac06d" : "#fdbb58",
            backgroundImage: i % 2 === 0 ? `url(${ludoImage})` : `url(${snakeImage})`,
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
          }}
          key={item}
        >
          {/* <h1>{item}</h1>
        </div>
      ))}
    </div>  */}
      <div
        className={`notification ${
          notifications ? " notification_active" : ""
        }`}
      >
        {notifications}
      </div>
    </div>
  );
  return <PageLayout content={content} />;
};

export default BuyChips;
