import React, { useEffect, useRef, useState } from "react";
import axios from "axios";
import { v4 as uuid } from "uuid";
import { socket } from "../App";
import Sound from "../assets/ChallengeAccepted.mpeg";
import {
  LoginPopup,
  CreateChallegePopup,
  AccaptChallengePopup,
} from "../Components";
import {
  Add as AddIcon,
  CloseSharp,
  ContentCopy as CopyIcon,
  // ReportGmailerrorred as ReportErrorIcon,
  CheckCircle as CheckIcon,
  ReportGmailerrorredOutlined,
} from "@mui/icons-material";
import {
  Avatar,
  Button,
  Divider,
  Fab,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  TextField,
} from "@mui/material";
import "./style.css";

const navMenu = ["Live Challenges", "My Challenges"];
const audio = new Audio(Sound);

const MainPageContent = () => {
  const [activeNav, setActiveNav] = useState(navMenu[0]);
  const [challenges, setChallenges] = useState([]);
  const [loginPopup, setLoginPopup] = useState(false);
  const [createChallegePopup, setCreateChallengePopup] = useState(false);
  const [warning, setWarning] = useState(false);
  const [waiting, setWaiting] = useState(false);
  const [win, setWin] = useState(false);
  const [codePopup, setCodePopup] = useState(false);
  const [acceptPopup, setAccptPopup] = useState(false);
  const [notifications, setNotification] = useState(false);
  const [reportChallenge, setReportChallenge] = useState(false);
  const [reportBtnTime, setReportBtnTime] = useState(0);
  const [reportBtn, setReportBtn] = useState(false);
  const myRef = useRef(null);
  const myuseRef = useRef(null);

  useEffect(() => {
    const recivemsg = (m) => {
      console.log(m);
      if (localStorage.getItem("user_uuid") === m.user_uuid) {
        audio.play();
        setCodePopup(m.challenge_uuid);
      }
      if (acceptPopup.challenge_uuid === m.challenge_uuid) {
        setAccptPopup(false);
        setNotification(m.msg);
        setTimeout(() => setNotification(false), 3000);
      }
      setReportBtn(m.challenge_uuid);
      setTimeout(() => setReportBtn(false), reportBtnTime * 1000);
    };
    socket.on("ChallengeAccepted", recivemsg);
    return () => socket.off("ChallengeAccepted", recivemsg);
    // eslint-disable-next-line
  }, [acceptPopup]);

  useEffect(() => {
    if (reportBtn) {
      let time = new Date();
      if (
        challenges?.find((a) => a.challenge_uuid === reportBtn)?.accepted_at +
          reportBtnTime * 1000 <
        time.getTime()
      ) {
        setReportBtn(false);
      }
    }
    // eslint-disable-next-line
  }, [challenges, reportBtn]);

  useEffect(() => {
    const recivemsg = (m) => {
      console.log(m);

      if (localStorage.getItem("user_uuid") === m.user_uuid) {
        // audio.play();
        setCodePopup("");
      }
      if (m.challenge_uuid && codePopup === m?.challenge_uuid) {
        setAccptPopup(false);
        setNotification(m.msg);
        setTimeout(() => setNotification(false), 3000);
      }
    };
    socket.on("ChallengeCanceled", recivemsg);
    return () => socket.off("ChallengeCanceled", recivemsg);
  }, [codePopup]);

  useEffect(() => {
    const recivemsg = (m) => {
      getChallenges();
      console.log(m, acceptPopup);
      if (
        m.challenge_uuid &&
        acceptPopup?.challenge_uuid === m?.challenge_uuid
      ) {
        setAccptPopup(false);
        setNotification(m.msg);
        setTimeout(() => setNotification(false), 3000);
      }
    };
    socket.on("update", recivemsg);
    return () => socket.off("update", recivemsg);
  }, [acceptPopup]);

  const executeScroll = () => myRef.current.scrollIntoView();

  const getChallenges = async () => {
    const result = await axios({
      method: "get",
      url:
        "/challenges/getActiveChallenges/" +
        (localStorage.getItem("user_uuid") || "test") +
        "/" +
        (window.location.pathname.includes("Ludo") ? "Ludo" : "Snake"),

      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setChallenges(result.data.result);
  };

  const getReportBtnTime = async () => {
    const result = await axios({
      method: "get",
      url: "/details/getReportBtnTime",
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setReportBtnTime(result.data.result);
  };

  useEffect(() => {
    getChallenges();
    getReportBtnTime();
  }, []);

  const CreatChallege = async (Challenge, ludo_king_username) => {
    setWaiting(Challenge.challenge_uuid);
    const result = await axios({
      method: "put",
      url: "/challenges/updateAccept",
      data: {
        challenge_uuid: Challenge.challenge_uuid,
        accepted_by: localStorage.getItem("user_uuid"),
        ludo_king_username: ludo_king_username,
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) {
      socket.emit("update", {});
      socket.emit("ChallengeAccepted", {
        name: "play",
        user_uuid: Challenge.created_by,

        challenge_uuid: Challenge.challenge_uuid,
        msg: "Challenge already accepted by someone",
      });
      setAccptPopup(false);
    }
    console.log(result);
    if (result.data.success === false) {
      setWarning("Your Balance is " + (result.data.coins || 0));
    }
    setWaiting(false);
    executeScroll();
  };

  const CancelChallege = async (Challenge) => {
    setWaiting(Challenge.challenge_uuid);
    const result = await axios({
      method: "put",
      url: "/challenges/updateCancel",
      data: {
        challenge_uuid: Challenge.challenge_uuid,
        accepted_by: localStorage.getItem("user_uuid"),
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) {
      socket.emit("ChallengeCanceled", {
        user_uuid: Challenge.created_by,
        challenge_uuid: Challenge.challenge_uuid,
        msg: "Challenge cancelled by other user",
      });
      socket.emit("update", {});
    }
    setWaiting(false);
    executeScroll();
  };

  const GameOneChallenge = async (Challenge) => {
    const result = await axios({
      method: "put",
      url: "/challenges/gameOnChallenge",
      data: {
        challenge_uuid: Challenge.challenge_uuid,
        room_code: Challenge.room_code,
        // accepted_by: localStorage.getItem("user_uuid"),
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) socket.emit("update", {});
    setCodePopup(false);
  };

  const updateVictory = async () => {
    const result = await axios({
      method: "put",
      url: "/challenges/updateVictory",
      data: {
        challenge_uuid: win.challenge_uuid,
        [win.type === "Won" ? "win" : "loss"]:
          localStorage.getItem("user_uuid"),
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    console.log(result);
    if (result.data.success) setWin(false);
    socket.emit("update", {});
  };

  const ReportOrder = async () => {
    const result = await axios({
      method: "put",
      url: "/challenges/reportOrder",
      data: {
        challenge_uuid: reportChallenge.challenge_uuid,
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setWin(false);
    setReportChallenge(false);
    socket.emit("update", {});
  };

  const CancelOrder = async (Challenge) => {
    const result = await axios({
      method: "put",
      url: "/challenges/terminateOrder",
      data: {
        challenge_uuid: Challenge.challenge_uuid,
        user_uuid: localStorage.getItem("user_uuid"),
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success)
      socket.emit("update", { challenge_uuid: Challenge.challenge_uuid });
  };

  const uploadImage = async (Challenge) => {
    console.log(Challenge);
    if (!Challenge.image || waiting === Challenge.challenge_uuid) return;
    setWaiting(Challenge.challenge_uuid);
    var bodyFormData = new FormData();
    const id = uuid();
    let fileData = new File(
      [Challenge.image],
      id + "." + (Challenge.image.name.split(".")[-1] || "png")
    );
    bodyFormData.append("image", fileData);
    bodyFormData.append(
      "value",
      JSON.stringify({
        challenge_uuid: Challenge.challenge_uuid,
        user_uuid: localStorage.getItem("user_uuid"),
      })
    );
    const response = await axios({
      method: "put",
      url: "/challenges/postImage",
      data: bodyFormData,
    });
    if (response.data.success) {
      console.log(response);
      setWaiting("");
      // socket.emit("update", {});
      updateVictory();
    }
  };

  // eslint-disable-next-line
  const TimeCheking = (time) => {
    let curr = new Date();
    curr = curr.getTime();
    return curr - time <= reportBtnTime * 1000;
  };

  return (
    <div className="main">
      <h1>Ludo King Challenge</h1>
      <h3>Classic Mode</h3>
      <div className="main_nav">
        {navMenu.map((item) => (
          <div
            className={`main_nav_content${
              activeNav === item ? " main_nav_content_active" : ""
            }`}
            onClick={() => {
              if (item !== "Live Challenges") {
                let user_uuid = localStorage.getItem("user_uuid");
                if (!user_uuid) {
                  setLoginPopup(true);
                  return;
                }
              }
              setActiveNav(item);
            }}
            key={item}
          >
            {item}
          </div>
        ))}
      </div>
      <div className="main_body">
        <List
          sx={{ width: "100%", padding: "0" }}
          style={{ background: "rgb(128 164 159 / 19%)" }}
        >
          {challenges.filter(
            (a) =>
              (activeNav === "Live Challenges"
                ? localStorage.getItem("user_uuid") !== a.created_by
                : localStorage.getItem("user_uuid") === a.created_by) &&
              a.challenge_uuid
          ).length ? (
            challenges
              .filter(
                (a) =>
                  (activeNav === "Live Challenges"
                    ? localStorage.getItem("user_uuid") !== a.created_by
                    : localStorage.getItem("user_uuid") === a.created_by) &&
                  a.challenge_uuid
              )
              .sort((a) => (+a.status === 1 ? 1 : -1))
              .map((item, i) => (
                <>
                  <ListItem
                    ref={i === 0 ? myRef : myuseRef}
                    key={item.challenge_uuid}
                    style={{
                      justifyContent: "space-between",
                      gap: "0.6rem",
                      padding: "5px",
                    }}
                  >
                    <ListItemAvatar style={{ minWidth: "auto" }}>
                      <Avatar style={{ background: "#44cd4a" }} />
                    </ListItemAvatar>
                    <ListItemText
                      style={{ fontSize: "0.9em" }}
                      primary={`${item.coins} Coins`}
                      secondary={
                        item?.created_username?.length > 7
                          ? item?.created_username.slice(0, 7) + "..."
                          : item?.created_username
                      }
                    />
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignItems: "center",
                        margin: "0 0.3rem",
                        flex: "1",
                        gap: "5px",
                        whiteSpace: "nowrap",
                      }}
                    >
                      {activeNav === "Live Challenges" &&
                      !(+item.status === 4 || +item.status === 5) ? (
                        <>
                          {TimeCheking(item.accepted_at) ? (
                            <div
                              className="report_btn"
                              onClick={() => setReportChallenge(item)}
                            >
                              <ReportGmailerrorredOutlined />
                            </div>
                          ) : (
                            ""
                          )}
                          {+item.status === 3 ? (
                            <div className="roomeCodeDiv">
                              <h6>
                                {item?.room_code?.length > 5
                                  ? item?.room_code.slice(0, 5) + "..."
                                  : item?.room_code}
                              </h6>
                              <CopyIcon
                                style={{ fontSize: "1rem" }}
                                onClick={() => {
                                  navigator.clipboard.writeText(item.room_code);
                                  setNotification("Room Code Copied");
                                  setTimeout(
                                    () => setNotification(false),
                                    3000
                                  );
                                  return;
                                }}
                              />
                            </div>
                          ) : null}

                          {+item.status === 3 &&
                          !item?.won_user?.filter(
                            (a) => a === localStorage.getItem("user_uuid")
                          ).length &&
                          !item?.loss_user?.filter(
                            (a) => a === localStorage.getItem("user_uuid")
                          ).length ? (
                            <div
                              style={{
                                display: "flex",
                                gap: "5px",
                                marginTop: "5px",
                                justifyContent: "space-between",
                              }}
                            >
                              <Button
                                variant="contained"
                                size="small"
                                onClick={() => {
                                  setWin({
                                    challenge_uuid: item.challenge_uuid,
                                    type: "Won",
                                  });
                                }}
                                disabled={!item.room_code}
                                style={{
                                  background: "#44cd4a",
                                  fontSize: "0.7em",
                                  height: "fit-content",
                                }}
                              >
                                Won
                              </Button>
                              <Button
                                variant="contained"
                                size="small"
                                onClick={() => {
                                  setWin({
                                    challenge_uuid: item.challenge_uuid,
                                    type: "Lost",
                                  });
                                }}
                                disabled={!item.room_code}
                                style={{
                                  backgroundColor: "red",
                                  fontSize: "0.7em",
                                  height: "fit-content",
                                }}
                              >
                                Lost
                              </Button>
                            </div>
                          ) : +item.status === 3 ? (
                            <h6 style={{ margin: 0 }}>
                              Waiting for
                              <br /> Other User...
                            </h6>
                          ) : (
                            ""
                          )}

                          {waiting === item?.challenge_uuid ||
                          [1, 2].includes(+item?.status) ? (
                            <Button
                              variant="contained"
                              size="small"
                              endIcon={
                                +item?.status === 1 ? <CheckIcon /> : null
                              }
                              style={
                                +item.status === 3
                                  ? {
                                      background: "#44cd4a",
                                      padding: "5px",
                                      height: "fit-content",
                                      width: "fit-content",
                                    }
                                  : {
                                      padding: "5px",
                                      height: "fit-content",
                                      width: "fit-content",
                                      background:
                                        +item.status === 2 ? "red" : "#44cd4a",
                                    }
                              }
                              onClick={() => {
                                let user_uuid =
                                  localStorage.getItem("user_uuid");
                                if (user_uuid) {
                                  if (+item.status === 2) CancelChallege(item);
                                  else setAccptPopup(item);
                                  return;
                                } else {
                                  setLoginPopup(true);
                                }
                              }}
                              disabled={waiting === item.challenge_uuid}
                            >
                              {waiting === item.challenge_uuid
                                ? "Waiting..."
                                : +item.status === 2
                                ? "Cancel"
                                : +item.status === 1
                                ? "Accept"
                                : ""}
                            </Button>
                          ) : null}
                        </>
                      ) : (
                        ""
                      )}

                      {+item.status === 3 &&
                      activeNav !== "Live Challenges" &&
                      !item?.won_user?.filter(
                        (a) => a === localStorage.getItem("user_uuid")
                      ).length &&
                      !item?.loss_user?.filter(
                        (a) => a === localStorage.getItem("user_uuid")
                      ).length ? (
                        <div
                          style={{
                            display: "flex",
                            gap: "5px",
                            marginTop: "5px",
                            justifyContent: "space-between",
                          }}
                        >
                          <Button
                            variant="contained"
                            size="small"
                            onClick={() => {
                              setWin({
                                challenge_uuid: item.challenge_uuid,
                                type: "Won",
                              });
                            }}
                            disabled={!item.room_code}
                            style={{
                              background: "#44cd4a",
                              fontSize: "0.7em",
                              height: "fit-content",
                            }}
                          >
                            Won
                          </Button>
                          <Button
                            variant="contained"
                            size="small"
                            onClick={() => {
                              setWin({
                                challenge_uuid: item.challenge_uuid,
                                type: "Lost",
                              });
                            }}
                            disabled={!item.room_code}
                            style={{
                              backgroundColor: "red",
                              fontSize: "0.7em",
                              height: "fit-content",
                            }}
                          >
                            Lost
                          </Button>
                        </div>
                      ) : +item.status === 3 &&
                        activeNav !== "Live Challenges" ? (
                        <h6 style={{ margin: 0 }}>
                          Waiting for
                          <br /> Other User...
                        </h6>
                      ) : (
                        ""
                      )}
                      {!item.room_code &&
                      localStorage.getItem("user_uuid") === item.created_by &&
                      +item.status === 2 ? (
                        <Button
                          variant="contained"
                          size="small"
                          endIcon={+item?.status === 1 ? <CheckIcon /> : null}
                          style={{
                            padding: "5px",
                            background: +item.status === 2 ? "red" : "#44cd4a",
                          }}
                          onClick={() => {
                            setCodePopup(item.challenge_uuid);
                          }}
                          disabled={waiting === item.challenge_uuid}
                        >
                          Enter Room Code
                        </Button>
                      ) : null}
                      {+item.status === 2 && activeNav === "Live Challenges" ? (
                        <h6 style={{ margin: 0 }}>Waiting for Room Code...</h6>
                      ) : (
                        ""
                      )}
                      {+item.status === 1 && activeNav === "My Challenges" ? (
                        <Button
                          variant="contained"
                          style={{
                            padding: "5px",
                            background: "red",
                          }}
                          size="small"
                          onClick={() => {
                            CancelOrder(item);
                          }}
                          disabled={waiting === item.challenge_uuid}
                        >
                          Cancel
                        </Button>
                      ) : (
                        ""
                      )}
                      {+item.status === 4 ? (
                        !item?.images?.filter(
                          (a) =>
                            a.user_uuid === localStorage.getItem("user_uuid")
                        ).length ? (
                          <div className="main_room_code">
                            <input
                              type="file"
                              accept="image/png, image/jpeg"
                              onChange={(e) =>
                                setChallenges((prev) =>
                                  prev.map((a) =>
                                    a.challenge_uuid === item.challenge_uuid
                                      ? { ...a, image: e.target.files[0] }
                                      : a
                                  )
                                )
                              }
                              autocomplete="off"
                            />
                            <Button
                              variant="contained"
                              size="small"
                              onClick={() => {
                                uploadImage(item);
                              }}
                              disabled={waiting === item.challenge_uuid}
                              style={{
                                background:
                                  waiting === item.challenge_uuid
                                    ? "red"
                                    : "#44cd4a",
                              }}
                            >
                              {waiting === item.challenge_uuid
                                ? "Wait"
                                : "Done"}
                            </Button>
                          </div>
                        ) : (
                          <h6 style={{ margin: 0 }}>Resolution pending...</h6>
                        )
                      ) : (
                        ""
                      )}
                    </div>
                    <ListItemText
                      primary={`${item.coins} Coins`}
                      secondary={
                        item?.accepted_username?.length > 7
                          ? item?.accepted_username.slice(0, 7) + "..."
                          : item?.accepted_username
                      }
                      style={{ textAlign: "end", fontSize: "0.9em" }}
                    />
                    <ListItemAvatar style={{ minWidth: "auto" }}>
                      <Avatar style={{ background: "#fdbb58" }} />
                    </ListItemAvatar>
                  </ListItem>
                  <Divider />
                </>
              ))
          ) : (
            <>
              <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <CloseSharp />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary="No Challenges" secondary="" />
              </ListItem>
              <Divider />
            </>
          )}
        </List>
      </div>

      <Fab
        onClick={() => {
          let user_uuid = localStorage.getItem("user_uuid");
          if (user_uuid) {
            setCreateChallengePopup(true);
          } else {
            setLoginPopup(true);
          }
        }}
        size="small"
        variant="extended"
        style={{
          background: "#fdbb58",
          color: "white",
          width: "fit-content",
          position: "absolute",
          bottom: "10px",
          right: "calc(50% - 100px)",
        }}
      >
        <AddIcon sx={{ mr: 1 }} />
        Create Challenge
      </Fab>
      {loginPopup ? (
        <LoginPopup
          close={() => {
            setLoginPopup(false);
            getChallenges();
          }}
        />
      ) : (
        ""
      )}
      {createChallegePopup ? (
        <CreateChallegePopup
          setNotification={setNotification}
          close={() => {
            setCreateChallengePopup(false);
            socket.emit("update", {});
          }}
          setWarning={setWarning}
        />
      ) : (
        ""
      )}
      {acceptPopup ? (
        <AccaptChallengePopup
          close={() => {
            setAccptPopup(false);
          }}
          onSave={(ludo_king_username) => {
            CreatChallege(acceptPopup, ludo_king_username);
          }}
          setAccptPopup={setAccptPopup}
        />
      ) : (
        ""
      )}
      {warning ? (
        <div className="overlay">
          <div className="popup_background">
            <div className="popup_nav">
              <h3>Please Add Coins</h3>
              <h3>{warning || ""}</h3>
            </div>
            <button
              className="popup_submit_btn"
              type="button"
              onClick={() => setWarning(false)}
              style={{
                padding: "5px",
                width: "fit-content",
              }}
            >
              Okay
            </button>
          </div>
        </div>
      ) : (
        ""
      )}
      {codePopup ? (
        <div className="overlay">
          <div className="popup_background">
            <div className="popup_nav">
              <h3>Please Enter Room Code</h3>
              <TextField
                autoFocus={true}
                id="outlined-size-small"
                size="small"
                placeholder="Enter Room Code"
                value={
                  challenges.find((item) => item.challenge_uuid === codePopup)
                    ?.room_code || ""
                }
                onChange={(e) =>
                  setChallenges((prev) =>
                    prev.map((a) =>
                      a.challenge_uuid === codePopup
                        ? { ...a, room_code: e.target.value }
                        : a
                    )
                  )
                }
              />
            </div>
            <button
              className="popup_submit_btn"
              type="button"
              style={{
                padding: "5px",
                width: "fit-content",
              }}
              onClick={() =>
                GameOneChallenge(
                  challenges.find((item) => item.challenge_uuid === codePopup)
                )
              }
              disabled={
                !challenges.find((item) => item.challenge_uuid === codePopup)
                  ?.room_code
              }
            >
              Done
            </button>
            <button
              className="popup_submit_btn"
              type="button"
              onClick={() => setCodePopup(false)}
              style={{ position: "absolute", right: 0, top: "-10px" }}
            >
              X
            </button>
          </div>
        </div>
      ) : (
        ""
      )}
      {win ? (
        <div className="overlay">
          <div className="popup_background">
            <div className="popup_nav">
              <h3>Please Confirm</h3>
              <h4 style={{ color: "#e72a41" }}>You {win.type || ""}</h4>
              <div className="main_room_code">
                <input
                  type="file"
                  accept="image/png, image/jpeg"
                  onChange={(e) =>
                    setWin((prev) => ({ ...prev, image: e.target.files[0] }))
                  }
                  autocomplete="off"
                />
              </div>
            </div>
            <div className="main_room_code">
              <button
                className="popup_submit_btn"
                type="button"
                onClick={() => uploadImage(win)}
                style={{
                  backgroundColor: "var(--main)",
                  padding: "5px",
                  width: "fit-content",
                }}
              >
                {waiting === win.challenge_uuid ? "Wait" : "Done"}
              </button>

              <button
                className="popup_submit_btn"
                type="button"
                onClick={() => setWin(false)}
                style={{
                  padding: "5px",
                  width: "fit-content",
                }}
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
      {reportChallenge ? (
        <div className="overlay">
          <div className="popup_background">
            <div className="popup_nav">
              <h3>
                Are you sure you want to
                <br /> Report this on-going Challenge ?
              </h3>
            </div>
            <div className="main_room_code">
              <button
                className="popup_submit_btn"
                type="button"
                onClick={ReportOrder}
                style={{
                  padding: "5px",
                  backgroundColor: "red",
                }}
              >
                Yes, Report
              </button>
              <button
                className="popup_submit_btn"
                type="button"
                onClick={() => setReportChallenge(false)}
                style={{
                  padding: "5px",
                  width: "fit-content",
                }}
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
      <div
        className={`notification ${
          notifications ? " notification_active" : ""
        }`}
      >
        {notifications}
      </div>
    </div>
  );
};

export default MainPageContent;
