import axios from "axios";
import React, { useEffect, useState } from "react";

const UserReportList = () => {
  const [users, setUsers] = useState([]);
  // const [filterUserName, setFilterUsername] = useState("");
  const [status, setStatus] = useState("all");
  const [challenge, setChallenge] = useState("all");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const getChallenges = async () => {
    const result = await axios({
      method: "get",
      url: "/challenges/getReportChallengesList",

      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success)
      setUsers(result.data.result.filter((a) => a.challenge_uuid));
  };
  useEffect(() => {
    getChallenges();
    // setStartDate(new Date().getTime());
    // setEndDate(new Date().getTime());
    return () => {};
  }, []);
  function formatAMPM(date) {
    var hours = date.getHours();
    var minutes = date.getMinutes();
    var ampm = hours >= 12 ? "pm" : "am";
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? "0" + minutes : minutes;
    var strTime = hours + ":" + minutes + " " + ampm;
    return strTime;
  }
  return users.length ? (
    <>
      <div className="searchContainer">
        <div className="inputContainer">
          <span>From:</span>
          <input
            type="date"
            onChange={(e) =>
              setStartDate(new Date(e.target.value + " 00:00:00 AM").getTime())
            }
            autocomplete="off"
            value={
              startDate
                ? "yy-mm-dd"
                    .replace(
                      "mm",
                      (
                        "00" + (new Date(startDate)?.getMonth() + 1).toString()
                      ).slice(-2)
                    )
                    .replace(
                      "yy",
                      (
                        "0000" + new Date(startDate)?.getFullYear().toString()
                      ).slice(-4)
                    )
                    .replace(
                      "dd",
                      ("00" + new Date(startDate)?.getDate().toString()).slice(
                        -2
                      )
                    )
                : ""
            }
            className="searchInput"
          />
        </div>
        <div className="inputContainer">
          <span>To:</span>
          <input
            type="date"
            onChange={(e) =>
              setEndDate(new Date(e.target.value + " 00:00:00 AM").getTime())
            }
            autocomplete="off"
            value={
              endDate
                ? "yy-mm-dd"
                    .replace(
                      "mm",
                      (
                        "00" + (new Date(endDate)?.getMonth() + 1).toString()
                      ).slice(-2)
                    )
                    .replace(
                      "yy",
                      (
                        "0000" + new Date(endDate)?.getFullYear().toString()
                      ).slice(-4)
                    )
                    .replace(
                      "dd",
                      ("00" + new Date(endDate)?.getDate().toString()).slice(-2)
                    )
                : ""
            }
            placeholder="Search User Name..."
            className="searchInput"
          />
        </div>
      </div>
      <div className="searchContainer">
        <div className="inputContainer">
          <span>Challenges:</span>
          <select
            className="numberInput"
            value={challenge}
            onChange={(e) => setChallenge(e.target.value)}
          >
            <option value="all">All</option>
            <option value="Snake">Snake</option>
            <option value="Ludo">Ludo</option>
          </select>
        </div>
        <div className="inputContainer">
          <span>Status:</span>
          <select
            className="numberInput"
            value={status}
            onChange={(e) => setStatus(e.target.value)}
          >
            <option value="all">All</option>
            <option value="0">Active</option>
            <option value="3">Game On</option>
            <option value="4">Resolution Pending</option>
            <option value="5">Completed</option>
            <option value="6">Expired</option>
          </select>
        </div>
      </div>

      <table className="user-table">
        <thead>
          <tr>
            <th colSpan={2}>
              <div className="t-head-element">Created At</div>
            </th>
            <th colSpan={3}>
              <div className="t-head-element">Played With</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Coins</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Commisson</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Status</div>
            </th>
          </tr>
        </thead>
        <tbody className="tbody">
          {users
            .sort((a, b) =>
              a.created_at
                ? b.created_at
                  ? a.created_at - b.created_at
                  : 1
                : 1
            )
            .filter((a) => !startDate || a?.created_at > startDate)
            .filter((a) => !endDate || a?.created_at < endDate + 86400000)
            .filter((a) => challenge === "all" || a?.game_id === challenge)
            .filter(
              (a) =>
                status === "all" ||
                (+status === 0
                  ? +a.status === 0 || +a.status === 1 || +a.status === 2
                  : a?.status === status)
            )
            .map((item, i) => (
              <tr
                key={item?.user_uuid || Math.random()}
                style={{
                  height: "30px",
                  fontSize: "12px",
                }}
              >
                <td colSpan={2}>{new Date(item.created_at).toDateString()} -{" "}
                {formatAMPM(new Date(item.created_at))}</td>
                <td colSpan={3}>{item.accepted_username || "-"}</td>
                <td colSpan={2}>{(item.coins || 0) * 2}</td>
                <td colSpan={2}>{item.commission || 0}</td>
                <td colSpan={2}>{item.status || 0}</td>
              </tr>
            ))}
        </tbody>
      </table>
    </>
  ) : (
    <div className="main_challeges_box">
      <div className="main_challeges_box_details">
        <h1>No Challenge</h1>
      </div>
    </div>
  );
};

export default UserReportList;
