import React, { useEffect, useState } from "react";
import TextField from "@mui/material/TextField";
import CloseIcon from "@mui/icons-material/Close";
import "./style.css";

import axios from "axios";

const AccaptChallengePopup = ({ close, onSave, setAccptPopup }) => {
  const [user, setUsers] = useState({});

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const GetUser = async () => {
    const result = await axios({
      method: "get",
      url: "/users/getDetails/" + localStorage.getItem("user_uuid"),
      data: user,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) {
      console.log(result.data);
      setUsers({
        ...result.data.userData,
        coins: result.data.userData.minimum,
      });
    }
  };

  useEffect(() => {
    GetUser();
    // eslint-disable-next-line
  }, []);

  return (
    <div className="overlay">
      <div className="popup_background">
        <CloseIcon className="popup_nav_icon" onClick={close} />
        <div className="popup_nav">
          <div className="balance-toast">
            <span>Balance:</span>
            <span>{user.coin_balance || ""}</span>
          </div>
          <TextField
            autoFocus={true}
            placeholder="Username"
            id="outlined-size-small"
            size="small"
            value={user?.ludo_king_username}
            onChange={(e) =>
              setUsers((prev) => ({
                ...prev,
                ludo_king_username: e.target.value,
              }))
            }
          />
        </div>
        <button
          className="popup_submit_btn"
          type="button"
          onClick={() => {
            if (!user.ludo_king_username) return;

            onSave(user.ludo_king_username);
          }}
        >
          Accept
        </button>
      </div>
    </div>
  );
};

export default AccaptChallengePopup;
