import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import CloseIcon from "@mui/icons-material/Close";
import "./style.css";
import axios from "axios";
const LoginPopup = ({ close }) => {
  const [user, setUsers] = useState({ user_mobile: "", username: "", otp: "" });
  const [varification, setVarification] = useState(false);
  const VarifyUser = async () => {
    const result = await axios({
      method: "post",
      url: "/users/login",
      data: user,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) {
      console.log(result.data);
      localStorage.setItem("user_uuid", result.data.result.user_uuid);
      close();
    }
  };
  const varifyOtp = async () => {
    const result = await axios({
      method: "post",
      url: "/users/varifyUser",
      data: user,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setVarification(true);
  };
  return (
    <div className="overlay">
      <div className="popup_background">
        <CloseIcon className="popup_nav_icon" onClick={close} />
        <div className="popup_nav">
          <h2>Login</h2>
          {varification ? (
            <TextField
              autoFocus={true}
              id="outlined-size-small"
              size="small"
              placeholder="OTP"
              value={user.otp}
              onChange={(e) =>
                setUsers((prev) => ({ ...prev, otp: e.target.value }))
              }
              autocomplete="off"
            />
          ) : (
            <TextField
              autoFocus={true}
              id="outlined-size-small"
              size="small"
              type="number"
              placeholder="Mobile Number"
              value={user.user_mobile}
              onChange={(e) =>
                setUsers((prev) => ({
                  ...prev,
                  user_mobile: e.target.value,
                }))
              }
              autocomplete="off"
            />
          )}
        </div>
        <button
          className="popup_submit_btn"
          type="button"
          onClick={varification ? VarifyUser : varifyOtp}
        >
          {varification ? "Confirm" : "Verify"}
        </button>
      </div>
    </div>
  );
};

export default LoginPopup;
