import React from "react";
import { useState } from "react";
import { motion } from "framer-motion";
import images from "../assets/Images";

let pages = [0, 1, 2, 3, 4, 5, 6];

function Test2() {
  const [position, setPosition] = useState(1);
  const [disabled, setDisabled] = useState(false);

  function checkScrollDirectionIsUp(event) {
    if (event.wheelDelta) {
      return event.wheelDelta > 0;
    }
    return event.deltaY < 0;
  }
  return (
    <div
      className="TestApp"
      onWheel={(e) => {
        if (disabled) {
          return;
        } else {
          if (checkScrollDirectionIsUp(e)) {
            console.log("UP");
            setPosition((prev) => (prev === 0 ? 0 : prev - 1));
            setDisabled(true);
            setTimeout(() => setDisabled(false), 1000);
          } else {
            console.log("Down");
            setPosition((prev) =>
              prev === pages.length - 1 ? prev : prev + 1
            );
            setDisabled(true);
            setTimeout(() => setDisabled(false), 1000);
          }
        }
      }}
    >
      <div className="row">
        {pages.map((a, i) => (
          <motion.div
            key={i}
            className="container"
            initial={{ rotate: 1 }}
            animate={{
              rotate: 0,

              top: `${(i - position) * 100 - 50}vh`,
            }}
            transition={{
              type: "tween",
              bounceStiffness: 260,
              bounceDamping: 20,
            }}
          >
            <img src={images[i]} alt="noImage" />
          </motion.div>
        ))}
      </div>
    </div>
  );
}

export default Test2;
