import { Check, CheckBox, Clear } from "@mui/icons-material";
import { Button } from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";

const PendingPaymentReport = () => {
  const [transections, setTransections] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  function formatAMPM(date) {
    var hours = date.getHours();
    var minutes = date.getMinutes();
    var ampm = hours >= 12 ? "pm" : "am";
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? "0" + minutes : minutes;
    var strTime = hours + ":" + minutes + " " + ampm;
    return strTime;
  }
  const getChallenges = async () => {
    const result = await axios({
      method: "get",
      url: "/transection/getTransectionsReport",

      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success)
      setTransections(result.data.result.filter((a) => a.transection_uuid));
  };
  const PutChallenge = async (data) => {
    const result = await axios({
      method: "put",
      url: "/transection/puttransaction",
      data,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) getChallenges();
  };
  useEffect(() => {
    getChallenges();
    // setStartDate(new Date().getTime());
    // setEndDate(new Date().getTime());
    return () => {};
  }, []);
  return transections.length ? (
    <>
      <div className="searchContainer">
        <div className="inputContainer">
          <span>From:</span>
          <input
            type="date"
            onChange={(e) =>
              setStartDate(new Date(e.target.value + " 00:00:00 AM").getTime())
            }
            autocomplete="off"
            value={
              startDate
                ? "yy-mm-dd"
                    .replace(
                      "mm",
                      (
                        "00" + (new Date(startDate)?.getMonth() + 1).toString()
                      ).slice(-2)
                    )
                    .replace(
                      "yy",
                      (
                        "0000" + new Date(startDate)?.getFullYear().toString()
                      ).slice(-4)
                    )
                    .replace(
                      "dd",
                      ("00" + new Date(startDate)?.getDate().toString()).slice(
                        -2
                      )
                    )
                : ""
            }
            className="searchInput"
          />
        </div>
        <div className="inputContainer">
          <span>To:</span>
          <input
            type="date"
            onChange={(e) =>
              setEndDate(new Date(e.target.value + " 00:00:00 AM").getTime())
            }
            autocomplete="off"
            value={
              endDate
                ? "yy-mm-dd"
                    .replace(
                      "mm",
                      (
                        "00" + (new Date(endDate)?.getMonth() + 1).toString()
                      ).slice(-2)
                    )
                    .replace(
                      "yy",
                      (
                        "0000" + new Date(endDate)?.getFullYear().toString()
                      ).slice(-4)
                    )
                    .replace(
                      "dd",
                      ("00" + new Date(endDate)?.getDate().toString()).slice(-2)
                    )
                : ""
            }
            placeholder="Search User Name..."
            className="searchInput"
          />
        </div>
      </div>

      <table className="user-table">
        <thead>
          <tr>
            <th colSpan={2}>
              <div className="t-head-element">Requested At</div>
            </th>
            <th colSpan={3}>
              <div className="t-head-element">User Name</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Amount</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Tansection Id</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Transection Type</div>
            </th>
            <th colSpan={2}>
              <div className="t-head-element">Actions</div>
            </th>
          </tr>
        </thead>
        <tbody className="tbody">
          {transections
            .sort((a, b) =>
              a.created_at
                ? b.created_at
                  ? a.created_at - b.created_at
                  : 1
                : 1
            )
            .filter((a) => !startDate || a?.created_at > startDate)
            .filter((a) => !endDate || a?.created_at < endDate + 86400000)

            .map((item, i) => (
              <tr
                key={item?.user_uuid || Math.random()}
                style={{
                  height: "30px",
                  fontSize: "12px",
                }}
              >
                <td colSpan={2}>
                  {new Date(item.created_at).toDateString()} -{" "}
                  {formatAMPM(new Date(item.created_at))}
                </td>
                <td colSpan={3}>{item.created_username || "-"}</td>
                <td colSpan={2}>{item.amount || 0}</td>
                <td colSpan={2}>{item.transection_id || 0}</td>
                <td colSpan={2}>{item.transection_type || 0}</td>
                <td colSpan={2}>
                  <div
                    className="flex"
                    style={{
                      fontSize: "15px",
                      justifyContent: "space-between",
                    }}
                  >
                    <div
                      className="action_icons"
                      onClick={() => PutChallenge({ ...item, approved: true })}
                    >
                      <Check fontSize="20" />
                    </div>
                    <div
                      className="action_icons"
                      onClick={() => PutChallenge({ ...item, approved: false })}
                    >
                      <Clear fontSize="20" />
                    </div>
                  </div>
                </td>
              </tr>
            ))}
        </tbody>
      </table>
    </>
  ) : (
    <div className="main_challeges_box">
      <div className="main_challeges_box_details">
        <h1>No Payments Pending</h1>
      </div>
    </div>
  );
};

export default PendingPaymentReport;
