import React, { useEffect, useState } from "react";
import {
  Avatar,
  Button,
  Divider,
  Fab,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  TextField,
} from "@mui/material";
import axios from "axios";
const Settings = ({ close }) => {
  const [details, setDetails] = useState({});
  const [editDetails, setEditeDetails] = useState(false);
  const [waiting, setWaiting] = useState(false);
  const getPrices = async () => {
    const result = await axios({
      method: "get",
      url: "/details/getDetails",
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setDetails(result.data.result);
    setEditeDetails(false)
  };
  const PutPrices = async () => {
    setWaiting(true);
    const result = await axios({
      method: "put",
      url: "/details/putDetails",
      data: editDetails,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) getPrices();
    setWaiting(false);
  };
  useEffect(() => {
    getPrices();
  }, []);
  return (
    <div className="overlay">
      <div className="popup_background">
        <h1 style={{ color: "black" }}>Settings</h1>
        <div className="popup_nav">
          <h3>Game Commisson</h3>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            size="small"
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            placeholder="Enter Room Code"
            value={details?.commission || ""}
            onChange={(e) => {
              setDetails((prev) => ({ ...prev, commission: e.target.value }));
              setEditeDetails((prev) => ({
                ...prev,
                commission: e.target.value,
              }));
            }}
          />
        </div>
        <div className="popup_nav">
          <h3>Coin Value</h3>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            size="small"
            placeholder="Enter Room Code"
            value={details?.one_coin_value || ""}
            onChange={(e) => {
              setDetails((prev) => ({
                ...prev,
                one_coin_value: e.target.value,
              }));
              setEditeDetails((prev) => ({
                ...prev,
                one_coin_value: e.target.value,
              }));
            }}
          />
        </div>
        <div className="popup_nav">
          <h3>Minimun Challenge Value</h3>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            size="small"
            placeholder="Enter Room Code"
            value={details?.minimum_challenge_value || ""}
            onChange={(e) => {
              setDetails((prev) => ({
                ...prev,
                minimum_challenge_value: e.target.value,
              }));
              setEditeDetails((prev) => ({
                ...prev,
                minimum_challenge_value: e.target.value,
              }));
            }}
          />
        </div>

        <div className="popup_nav">
          <h3>Repot Challenge Time</h3>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            size="small"
            placeholder="Enter Room Code"
            value={details?.report_challenge_time || ""}
            onChange={(e) => {
              setDetails((prev) => ({
                ...prev,
                report_challenge_time: e.target.value,
              }));
              setEditeDetails((prev) => ({
                ...prev,
                report_challenge_time: e.target.value,
              }));
            }}
          />
        </div>
        <div className="popup_nav">
          <h3>Auto Decision Time</h3>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault();
              }
            }}
            size="small"
            placeholder="Enter Room Code"
            value={details?.auto_decision_time || ""}
            onChange={(e) => {
              setDetails((prev) => ({
                ...prev,
                auto_decision_time: e.target.value,
              }));
              setEditeDetails((prev) => ({
                ...prev,
                auto_decision_time: e.target.value,
              }));
            }}
          />
        </div>
        <div className="popup_nav">
          <h3>Templete Challenges</h3>
          <div className="price_templetes_container">
            {details?.template_challenges?.map((a) => (
              <TextField
                key={a}
                autoFocus={true}
                id="outlined-size-small"
                size="small"
                placeholder="Enter Room Code"
                value={a || 0}
                onKeyPress={(event) => {
                  if (!/[0-9]/.test(event.key)) {
                    event.preventDefault();
                  }
                }}
                onChange={(e) => {
                  setDetails((prev) => ({
                    ...prev,
                    template_challenges: prev.template_challenges.map((b) =>
                      a === b ? e.target.value : b
                    ),
                  }));
                  setEditeDetails((prev) => ({
                    ...prev,
                    template_challenges: details.template_challenges.map((b) =>
                      a === b ? e.target.value : b
                    ),
                  }));
                }}
              />
            ))}
          </div>
        </div>
        <button
          className="popup_submit_btn"
          type="button"
          style={{
            padding: "5px",
            width: "fit-content",
            opacity: !editDetails || waiting ? "0.5" : "1",
          }}
          onClick={() => PutPrices()}
          disabled={!editDetails || waiting}
        >
          {waiting ? "Updating..." : "Update"}
        </button>
        <button
          className="popup_submit_btn"
          type="button"
          onClick={() => close()}
          style={{ position: "absolute", right: 0, top: "-10px" }}
        >
          X
        </button>
      </div>
    </div>
  );
};

export default Settings;
