import axios from "axios";
import React, { useEffect, useState } from "react";

const UserReportList = () => {
  const [users, setUsers] = useState([]);
  const [filterUserName, setFilterUsername] = useState("");
  const [filterUserMobile, setFilterUserModile] = useState("");
  const [LudoPlay, setLudoPlay] = useState(false);
  const [sankePlay, setSnakePlay] = useState(false);

  const getChallenges = async () => {
    const result = await axios({
      method: "get",
      url: "/users/getReportUsers/",

      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success)
      setUsers(result.data.result.filter((a) => a.user_uuid));
  };

  const getLudoChallenges = async (game_id) => {
    const result = await axios({
      method: "get",
      url: "/users/getReportUsers/" + game_id,

      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success)
      setUsers((prev) =>
        prev.map((a) => {
          let data = result.data.result.find(
            (b) => b.item_uuid === a.item_uuid
          );
          return {
            ...a,
            ...data,
          };
        })
      );
  };

  useEffect(() => {
    if (LudoPlay) {
      getLudoChallenges("Ludo");
    }
  }, [LudoPlay]);

  useEffect(() => {
    if (sankePlay) {
      getLudoChallenges("Snake");
    }
  }, [sankePlay]);

  useEffect(() => {
    getChallenges();
    return () => {};
  }, []);
  function formatAMPM(date) {
    var hours = date.getHours();
    var minutes = date.getMinutes();
    var ampm = hours >= 12 ? "pm" : "am";
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? "0" + minutes : minutes;
    var strTime = hours + ":" + minutes + " " + ampm;
    return strTime;
  }
  return users.length ? (
    <>
      <div className="searchContainer">
        <input
          type="text"
          onChange={(e) => setFilterUsername(e.target.value)}
          value={filterUserName}
          placeholder="Search User Name..."
          className="searchInput"
          autocomplete="off"
        />
        <input
          type="text"
          onChange={(e) => setFilterUserModile(e.target.value)}
          value={filterUserMobile}
          placeholder="Search Mobile..."
          className="searchInput"
          autocomplete="off"
        />
      </div>
      <div className="searchContainer">
        <button
          className={`playsView ${LudoPlay ? "playsViewActive" : ""}`}
          type="button"
          onClick={() => setLudoPlay((prev) => !prev)}
        >
          Ludo Plays
        </button>

        <button
          className={`playsView ${sankePlay ? "playsViewActive" : ""}`}
          type="button"
          onClick={() => setSnakePlay((prev) => !prev)}
        >
          Snake Plays
        </button>
      </div>
      <div className="table-container">
        <table className="user-table">
          <thead>
            <tr>
              <th colSpan={3}>
                <div className="t-head-element">Created At</div>
              </th>
              <th colSpan={3}>
                <div className="t-head-element">User Mobile</div>
              </th>
              <th colSpan={2}>
                <div className="t-head-element">User Name</div>
              </th>
              <th colSpan={2}>
                <div className="t-head-element">Wallet Balance</div>
              </th>
              <th colSpan={2}>
                <div className="t-head-element">Positive</div>
              </th>
              <th colSpan={2}>
                <div className="t-head-element">Nagative</div>
              </th>
              <th colSpan={2}>
                <div className="t-head-element">Cancellation</div>
              </th>
              {LudoPlay ? (
                <th colSpan={2}>
                  <div className="t-head-element">Ludo Play</div>
                </th>
              ) : (
                ""
              )}
              {sankePlay ? (
                <th colSpan={2}>
                  <div className="t-head-element">Snake Play</div>
                </th>
              ) : (
                ""
              )}
            </tr>
          </thead>
          <tbody className="tbody">
            {users
              .sort((a, b) =>
                a.created_at
                  ? b.created_at
                    ? a.created_at - b.created_at
                    : 1
                  : 1
              )
              .filter(
                (a) =>
                  !filterUserName ||
                  a.ludo_king_username
                    ?.toLocaleLowerCase()
                    .includes(filterUserName.toLocaleLowerCase())
              )
              .filter(
                (a) =>
                  !filterUserMobile ||
                  a?.user_mobile
                    ?.toLocaleLowerCase()
                    .includes(filterUserMobile.toLocaleLowerCase())
              )
              .map((item, i) => (
                <tr
                  key={item?.user_uuid || Math.random()}
                  style={{
                    height: "30px",
                    fontSize: "12px",
                  }}
                >
                  <td colSpan={3}>{new Date(item.created_at).toDateString()} -{" "}
                {formatAMPM(new Date(item.created_at))}</td>
                  <td colSpan={3}>{item.user_mobile || "-"}</td>
                  <td colSpan={2}>{item.ludo_king_username || "-"}</td>
                  <td colSpan={2}>{item.coin_balance || 0}</td>
                  <td colSpan={2}>{item.positive_score || 0}</td>
                  <td colSpan={2}>{item.negative_score || 0}</td>
                  <td colSpan={2}>{item.cancellation_score || 0}</td>
                  {LudoPlay ? (
                    <td colSpan={2}>
                      <span style={{ color: "var(--main)" }}>
                        {item.ludoWin || 0}
                      </span>
                      <br />
                      <span style={{ color: "red" }}>{item.ludoLoss || 0}</span>
                    </td>
                  ) : (
                    ""
                  )}
                  {sankePlay ? (
                    <td colSpan={2}>
                      <span style={{ color: "var(--main)" }}>
                        {item.snakeWin || 0}
                      </span>
                      <br />
                      <span style={{ color: "red" }}>
                        {item.snakeLoss || 0}
                      </span>
                    </td>
                  ) : (
                    ""
                  )}
                </tr>
              ))}
          </tbody>
        </table>
      </div>
    </>
  ) : (
    <div className="main_challeges_box">
      <div className="main_challeges_box_details">
        <h1>No Users</h1>
      </div>
    </div>
  );
};

export default UserReportList;
