import React, { useState } from "react";
import PageLayout from "./PageLayout";
import { SportsEsports as GameIcon } from "@mui/icons-material";
import ludoImage from "../assets/ludo.png";
import snakeImage from "../assets/snakeandladder.png";

const navMenu = ["Games"];
const games = ["Ludo", "Snake"];

const Dashboard = () => {
  const [activeNav, setActiveNav] = useState(navMenu[0]);

  const content = (
    <div className="main">
      <h1>Ludo King Challenge</h1>
      <h3>Classic Mode</h3>
      <div className="main_nav">
        {navMenu.map((item) => (
          <div
            className={`main_nav_content${
              activeNav === item ? " main_nav_content_active" : ""
            }`}
            onClick={() => {
              setActiveNav(item);
            }}
            key={item}
          >
            {" "}
            <GameIcon sx={{ marginRight: "8px" }} />
            {item}
          </div>
        ))}
      </div>
      <div className="dashboard_body">
        {games.map((item, i) => (
          <div
            className="dashboard_challeges_box"
            onClick={() => window.location.assign(`/${item}Main`)}
            style={{
              // background: i % 2 === 0 ? "#8ac06d" : "#fdbb58",
              backgroundImage: i % 2 === 0 ? `url(${ludoImage})` : `url(${snakeImage})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            key={item}
          >
            {/* <h1>{item}</h1> */}
          </div>
        ))}
      </div>
    </div>
  );
  return <PageLayout content={content} />;
};

export default Dashboard;
