import React, { useEffect, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";
import TextField from "@mui/material/TextField";
import axios from "axios";
import "./style.css";

const CreateChallegePopup = ({ close, setWarning,setNotification }) => {
  const [user, setUsers] = useState({});
  const [prices, setPrices] = useState([]);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const GetUser = async () => {
    const result = await axios({
      method: "get",
      url: "/users/getDetails/" + localStorage.getItem("user_uuid"),
      data: user,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) {
      console.log(result.data);
      setUsers({
        ...result.data.userData,
        coins: result.data.userData.minimum,
      });
    }
  };

  const getPrices = async () => {
    const result = await axios({
      method: "get",
      url: "/details/getPricesTemplets",
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) setPrices(result.data.result);
  };

  useEffect(() => {
    GetUser();
    getPrices();
    // eslint-disable-next-line
  }, []);

  const CreatChallege = async () => {
    // audio.play();
    if (
      user.coins < +user.minimum ||
      !user.ludo_king_username ||
      user.coins % 10
    )
      return;
    if (user.coins > (+user.coin_balance || 0)) {
      setWarning("Your Balance is " + (user.coin_balance || 0));
      return;
    }
    const result = await axios({
      method: "post",
      url: "/challenges/create",
      data: {
        user_uuid: localStorage.getItem("user_uuid"),
        ludo_king_username: user.ludo_king_username,
        coins: user.coins - (user.coins % 10),
        game_id: window.location.pathname.includes("Ludo") ? "Ludo" : "Snake",
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (result.data.success) close();
    if (result.data.success===false) setNotification(result.data.message);
  };

  return (
    <div className="overlay">
      <div className="popup_background" style={{ paddingTop: "30px" }}>
        <div className="popup_nav">
          <div
            style={{
              display: "flex",
              gap: "10px",
              width: "-webkit-fill-available",
              alignItems: "center",
            }}
          >
            <div className="balance-toast" style={{ flex: "1" }}>
              <span>Balance:</span>
              <span>{user.coin_balance || ""}</span>
            </div>
            <CloseIcon
              className="popup_nav_icon"
              onClick={close}
              style={{ position: "inherit" }}
            />
          </div>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            size="small"
            placeholder="Username"
            value={user?.ludo_king_username}
            onChange={(e) =>
              setUsers((prev) => ({
                ...prev,
                ludo_king_username: e.target.value,
              }))
            }
          />
          <TextField
            id="outlined-size-small"
            size="small"
            type="number"
            placeholder="Coins"
            defaultValue={user?.coins}
            value={user?.coins}
            onChange={(e) =>
              setUsers((prev) => ({
                ...prev,
                coins: e.target.value,
              }))
            }
          />
          <h5
            style={{
              color: "red",
              fontWeight: "500",
              visibility: user.coins % 10 ? "visible" : "hidden",
            }}
          >
            Only multiples of 10 allowed
          </h5>
          <div className="price_templetes_container">
            {prices.map((a) => (
              <div
                className={`price_templetes ${
                  +user.coins === +a ? "price_templetes_active" : ""
                }`}
                onClick={() =>
                  setUsers((prev) => ({
                    ...prev,
                    coins: a,
                  }))
                }
                key={a}
              >
                {a}
              </div>
            ))}
          </div>
        </div>
        <button
          className="popup_submit_btn"
          type="button"
          onClick={CreatChallege}
        >
          Create
        </button>
      </div>
    </div>
  );
};

export default CreateChallegePopup;
