import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import "./style.css";
import axios from "axios";

const admin_uuid = "030822";
const AdminLogin = ({ close }) => {
  const [user, setUsers] = useState("");

  const VarifyUser = async () => {
    if (user) {
      const result = await axios({
        method: "post",
        url: "/users/varifyAdmin",
        data: {user},
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (result.data.success) {
        sessionStorage.setItem("admin_uuid", admin_uuid);
        close();
      }
    } else {
      return;
    }
  };

  return (
    <div className="overlay" style={{ zIndex: 9999 }}>
      <div className="popup_background">
        {/* <CloseIcon className="popup_nav_icon" onClick={close} /> */}
        <div className="popup_nav">
          <h2>Admin Login</h2>
          <TextField
            autoFocus={true}
            id="outlined-size-small"
            size="small"
            placeholder="Admin ID"
            value={user}
            onChange={(e) => setUsers(e.target.value)}
          />
        </div>
        <button className="popup_submit_btn" type="button" onClick={VarifyUser}>
          Login
        </button>
      </div>
    </div>
  );
};

export default AdminLogin;
