import React, { useEffect, useState } from "react";
import {
  AdminLogin,
  ChallengeReport,
  PendingChallengesList,
  ReportChallengesAdmin,
  UserReportList,
} from "../Components";
import SettingsIcon from "@mui/icons-material/Settings";
import Settings from "../Components/Settings";
import PendingPaymentReport from "../Components/PendingPaymentReport";
const navMenu = ["Users Reports", "Challenges Reports", "Pending Challenges","Payment Conferma-tion"];

const AdminPageContent = () => {
  const [activeNav, setActiveNav] = useState();
  const [loginPopup, setLoginPopup] = useState(false);
  const [settingPopup, setSettingPopup] = useState(false);
  const [selectiveChallenge, setSelectiveChallenge] = useState();

  useEffect(() => {
    if (sessionStorage.getItem("admin_uuid") && !loginPopup)
      setActiveNav(navMenu[0]);
    else setLoginPopup(true);
  }, [loginPopup]);
  return (
    <div className="main">
      <h1>Ludo King Challenge</h1>
      <h3>Admin</h3>
      <div className="setting_icons" onClick={()=>setSettingPopup(true)}><SettingsIcon /></div>
      
      <div className="main_nav">
        {navMenu.map((item) => (
          <div
            className={`main_nav_content${
              activeNav === item ? " main_nav_content_active" : ""
            }`}
            onClick={() => {
              setActiveNav(item);
            }}
            key={item}
          >
            {item}
          </div>
        ))}
      </div>
      <div className="main_body" style={{ paddingBottom: "0.5rem" }}>
        {activeNav === "Pending Challenges" ? (
          <PendingChallengesList
            setSelectiveChallenge={setSelectiveChallenge}
          />
        ) : activeNav === "Challenges Reports" ? (
          <ReportChallengesAdmin />
        ) : activeNav === "Users Reports" ? (
          <UserReportList />
        ) : activeNav === "Payment Conferma-tion" ? (
          <PendingPaymentReport />
        ) : (
          ""
        )}
      </div>

      {loginPopup ? (
        <AdminLogin
          close={() => {
            setLoginPopup(false);
          }}
        />
      ) : (
        ""
      )}
      {settingPopup ? (
        <Settings
          close={() => {
            setSettingPopup(false);
          }}
        />
      ) : (
        ""
      )}
      {selectiveChallenge ? (
        <ChallengeReport
          close={() => {
            setSelectiveChallenge(false);
          }}
          challenge={selectiveChallenge}
        />
      ) : (
        ""
      )}
    </div>
  );
};

export default AdminPageContent;
